



























import Vue from "vue";
import {Component, Prop} from "vue-property-decorator";

@Component
export default class MyPrompt extends Vue {
    showModal = false;
    @Prop({default: ""})
    readonly newName!: string;

    onAddNewName(value:string){
        this.$emit("update:newName",value);
    }

    onSubmit(){
        this.$emit("submit:newName");
    }
}
